.new-collections{
    display: flex;
    flex-direction:column;
    align-items: center;
    gap: 20px;
    margin-bottom: 100px;
}
.new_collections h1{
    color: #171717;
    font-size:  50px;
    font-weight: 600;
}

.new_collections hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background-color: #252525;
}
.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 30px;

}
.shopcategory-loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
}
